import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';


export interface Stats {
  PlayerID: any;
  Name: any;
  Position: any;
  Ingame: any;
  Icon: any;
}

export interface Account {
  accountID: Number;
  ingame: string;
  flex: boolean;
  solo: boolean;
  riot_accountid: string;
}

export interface Team {
  playerid: Number;
  ingame: string;
  name: string;
  position: string;
}

export interface PlayerStats{
  timestamp: any;
  Player: any;
  Champion: any;
  Gegner: any;
  Spielseite: any;
  Win: any;
  Gold: any;
  Kills: any;
  Deaths: any;
  Assists: any;
  Damage: any;
  Wards: any;
  Pinks: any;
  Wards_Cleared: any;
  Vision_Score: any;
  CS: any;
  Game_length: any;
  Total_kill: any;
  Total_death: any;
  Total_damage: any;
  MatchID: any;
}

@Injectable()
export class StatsService {

  constructor(private http: HttpClient) { }


  getStats(){
    return this.http.get<any>('assets/players.json')
    .toPromise()
    .then(res => <Stats[]> res.data)
    .then(data => {return data;});
  }

  getAccounts(query: string): Observable<Account[]>{
    const params = new HttpParams().append('query', query);
    return this.http.get<Account[]>('http://localhost:8080/accounts', {params});
  }

  getTeam(): Observable<Team[]>{
    return this.http.get<Team[]>('http://localhost:8080/team');
  }

  updateApiKey(key: String){
    return this.http.post('http://localhost:8080/key', key);
  }

  getPlayerStats(player: String){
    return this.http.get<any>('assets/stats_'+player+'.json')
    .toPromise()
    .then(res => <PlayerStats[]> res.data)
    .then(data => {return data;});
  }

  setAccount(account: Account){
    let data = {
      accountID: account.accountID,
      flex: account.flex,
      solo: account.solo
    }
    this.http.put("http://localhost:8080/accounts", data).toPromise().then(data =>{
      console.log(data);    });
    //console.log(this.http.put("http://localhost:8080/accounts", account, {params}))
  }
}
